export default {
	data() {
		return {
			modalName: null,
		}
	},
	methods: {
		call() {
			uni.makePhoneCall({
				phoneNumber: '15510007550'
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		// 跳转外部链接
		tonewurl(url) {
			console.info(url)
			if (url == null) {
				return false;
			}
			uni.navigateTo({
				url: '../webpage/webpage?url='+url
			});
		},
	}
}
